import React, { useState } from 'react';
import { Typography } from '../../global/fonts/typography';
import { filterByCategory } from '../../helpers/filters';

export const ListItem = ({ cat }) => {
  const [select, setSelect] = useState(false);
  const [nombre, setNombre] = useState('');

  const setNameCat = (item, status) => {
    setNombre(item);
    if (item === nombre) {
      setNombre('');
      setSelect(!status);
    }
  };

  return (
    <>
      <div className="card mt-3 mb-1" >
        {
          cat.img ? (
            <>
              <img className="card-img" src={cat.img} />
              <div className="card-img-overlay d-flex flex-column justify-content-center">
                <div className="d-flex flex justify-content-between align-items-baseline">
                  <h2 className="text-category pl-4" style={Typography.category}>
                    {cat.nombre}
                  </h2>
                  <button
                    style={Typography.seeMenu}
                    onClick={() => setNameCat(cat.nombre, !select)}
                    className="btn btn-sm size-btn">
                    {nombre === cat.nombre ? '-' : '+'}
                  </button>
                </div>
              </div>
            </>
          ) : (
            <div className="no-image__content">
              <div className="no-image__content-overlay d-flex flex justify-content-between align-items-baseline">
                <h2 className="text-category pl-4" style={Typography.category}>
                  {cat.nombre}
                </h2>
                <button
                  style={Typography.seeMenu}
                  onClick={() => setNameCat(cat.nombre, !select)}
                  className="btn btn-sm size-btn">
                  {nombre === cat.nombre ? '-' : '+'}
                </button>
              </div>

            </div>
          )
        }

      </div>

      {
        nombre === cat.nombre &&
        (
          <div className="animate__animated animate__fadeIn">
            <div className="mt-3">
              {
                filterByCategory(cat.nombre, cat.tipo).map((rs) => (
                  <div key={rs.nombre}>
                    <div className="d-flex justify-content-between">
                      <p style={Typography.plateTitleText} >{rs.nombre}</p>
                      {rs.precio &&
                        <p style={Typography.plateTitleText}>
                          ${rs.precio}
                        </p>
                      }
                    </div>
                    <div>
                      <p style={Typography.plateBodyText}>{rs.descripcion}</p>
                    </div>
                  </div>
                ))
              }
              <h3 style={Typography.plateTitleText}></h3>
            </div>
          </div>
        )
      }

    </>
  );
};